import { useMemo, useState } from 'react';
import { BOARD_COLOR_OPTIONS, BOARD_ICON_OPTIONS } from '../../data/constants';

const BOARDS_PER_PAGE = 6;

const EMPTY_BOARD_DRAFT = {
  name: '',
  icon: BOARD_ICON_OPTIONS[0],
  colorId: BOARD_COLOR_OPTIONS[0].id
};

export default function TrackerPage({
  boards,
  activeBoardId,
  onSetActiveBoard,
  onCreateBoard,
  onUpdateBoard,
  onDeleteBoard,
  taskForm,
  onTaskFormChange,
  onCreateTask,
  tasks,
  allTasks = [],
  onTaskStatusChange,
  onTaskDelete,
  priorityMeta,
  statusMeta
}) {
  const [dragTaskId, setDragTaskId] = useState(null);
  const [dragOverStatus, setDragOverStatus] = useState(null);
  const [isCreateTaskModalOpen, setCreateTaskModalOpen] = useState(false);
  const [isBoardModalOpen, setBoardModalOpen] = useState(false);
  const [boardModalMode, setBoardModalMode] = useState('create');
  const [editingBoardId, setEditingBoardId] = useState(null);
  const [boardDraft, setBoardDraft] = useState(EMPTY_BOARD_DRAFT);
  const [viewMode, setViewMode] = useState('boards');
  const [boardPage, setBoardPage] = useState(1);

  const selectedBoard = useMemo(
    () => boards.find((board) => board.id === activeBoardId) || null,
    [boards, activeBoardId]
  );

  const selectedBoardColor = useMemo(
    () => BOARD_COLOR_OPTIONS.find((color) => color.id === selectedBoard?.colorId) || BOARD_COLOR_OPTIONS[0],
    [selectedBoard?.colorId]
  );

  const boardTaskStats = useMemo(() => {
    const statsByBoard = {};

    boards.forEach((board) => {
      statsByBoard[board.id] = {
        total: 0,
        done: 0,
        progress: 0,
        todo: 0
      };
    });

    allTasks.forEach((task) => {
      if (!task?.boardId || !statsByBoard[task.boardId]) {
        return;
      }

      statsByBoard[task.boardId].total += 1;
      if (task.status === 'done') {
        statsByBoard[task.boardId].done += 1;
      } else if (task.status === 'progress') {
        statsByBoard[task.boardId].progress += 1;
      } else {
        statsByBoard[task.boardId].todo += 1;
      }
    });

    return statsByBoard;
  }, [allTasks, boards]);

  const globalBoardStats = useMemo(() => {
    const done = allTasks.filter((task) => task.status === 'done').length;
    const progress = allTasks.filter((task) => task.status === 'progress').length;

    return {
      boardsCount: boards.length,
      tasksCount: allTasks.length,
      done,
      progress
    };
  }, [allTasks, boards.length]);

  const boardPagination = useMemo(() => {
    const totalPages = Math.max(1, Math.ceil(boards.length / BOARDS_PER_PAGE));
    const safePage = Math.min(boardPage, totalPages);
    const start = (safePage - 1) * BOARDS_PER_PAGE;

    return {
      page: safePage,
      totalPages,
      items: boards.slice(start, start + BOARDS_PER_PAGE)
    };
  }, [boardPage, boards]);

  const stats = useMemo(
    () => ({
      total: tasks.length,
      done: tasks.filter((task) => task.status === 'done').length
    }),
    [tasks]
  );

  const handleDrop = async (targetStatus) => {
    if (!dragTaskId) {
      return;
    }
    await onTaskStatusChange(dragTaskId, targetStatus);
    setDragTaskId(null);
    setDragOverStatus(null);
  };

  const openCreateTaskModal = () => setCreateTaskModalOpen(true);
  const closeCreateTaskModal = () => setCreateTaskModalOpen(false);

  const openCreateBoardModal = () => {
    setBoardModalMode('create');
    setEditingBoardId(null);
    setBoardDraft(EMPTY_BOARD_DRAFT);
    setBoardModalOpen(true);
  };

  const openEditBoardModal = (board) => {
    setBoardModalMode('edit');
    setEditingBoardId(board.id);
    setBoardDraft({
      name: board.name,
      icon: board.icon,
      colorId: board.colorId
    });
    setBoardModalOpen(true);
  };

  const closeBoardModal = () => {
    setBoardModalOpen(false);
    setBoardModalMode('create');
    setEditingBoardId(null);
    setBoardDraft(EMPTY_BOARD_DRAFT);
  };

  const submitCreateTask = async (event) => {
    event.preventDefault();
    if (!taskForm.title.trim()) {
      return;
    }
    await onCreateTask(event);
    closeCreateTaskModal();
  };

  const submitBoard = async (event) => {
    event.preventDefault();

    const saved = boardModalMode === 'edit'
      ? await onUpdateBoard(editingBoardId, boardDraft)
      : await onCreateBoard(boardDraft);

    if (saved) {
      closeBoardModal();
      setViewMode('tasks');
    }
  };

  const handleDeleteBoard = async (board) => {
    const confirmDelete = window.confirm(`  "${board.name}"?      .`);
    if (!confirmDelete) {
      return;
    }

    const saved = await onDeleteBoard(board.id);
    if (saved) {
      setViewMode('boards');
    }
  };

  const openBoard = async (boardId) => {
    await onSetActiveBoard(boardId);
    setViewMode('tasks');
  };

  if (viewMode === 'boards') {
    return (
      <>
        <div className="tracker-boards-wrap">
          <div className="tracker-boards-head card shadow-sm">
            <div className="card-body p-4 p-lg-5">
              <div className="tracker-hero-grid">
                <div>
                  <h4 className="mb-2">  </h4>
                  <p className="text-body-secondary mb-4">
                        , ,    .
                        ,   .
                  </p>
                  <button type="button" className="btn btn-primary btn-lg" onClick={openCreateBoardModal}>
                    <i className="bi bi-plus-circle me-2" />
                     
                  </button>
                </div>

                <div className="tracker-hero-stats">
                  <div className="tracker-hero-stat">
                    <div className="small text-body-secondary"></div>
                    <div className="tracker-hero-value">{globalBoardStats.boardsCount}</div>
                  </div>
                  <div className="tracker-hero-stat">
                    <div className="small text-body-secondary"> </div>
                    <div className="tracker-hero-value">{globalBoardStats.tasksCount}</div>
                  </div>
                  <div className="tracker-hero-stat">
                    <div className="small text-body-secondary"> </div>
                    <div className="tracker-hero-value">{globalBoardStats.progress}</div>
                  </div>
                  <div className="tracker-hero-stat">
                    <div className="small text-body-secondary"></div>
                    <div className="tracker-hero-value">{globalBoardStats.done}</div>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <div className="row g-4 mt-1">
            {boardPagination.items.map((board) => {
              const color = BOARD_COLOR_OPTIONS.find((item) => item.id === board.colorId) || BOARD_COLOR_OPTIONS[0];
              const boardStats = boardTaskStats[board.id] || { total: 0, done: 0, progress: 0 };

              return (
                <div className="col-sm-6 col-xl-4" key={board.id}>
                  <article
                    className="tracker-board-card"
                    style={{ '--board-accent': color.accent, '--board-soft': color.soft }}
                    role="button"
                    tabIndex={0}
                    onClick={() => openBoard(board.id)}
                    onKeyDown={(event) => {
                      if (event.key === 'Enter' || event.key === ' ') {
                        event.preventDefault();
                        openBoard(board.id);
                      }
                    }}
                  >
                    <div className="tracker-board-card-top">
                      <div className="tracker-board-icon" style={{ color: color.accent }}>
                        <i className={`bi ${board.icon}`} />
                      </div>

                      <div className="tracker-board-actions" onClick={(event) => event.stopPropagation()}>
                        <button
                          type="button"
                          className="btn btn-sm btn-light"
                          title=" "
                          onClick={() => openEditBoardModal(board)}
                        >
                          <i className="bi bi-pencil" />
                        </button>
                        <button
                          type="button"
                          className="btn btn-sm btn-light text-danger"
                          title=" "
                          onClick={() => handleDeleteBoard(board)}
                        >
                          <i className="bi bi-trash" />
                        </button>
                      </div>
                    </div>

                    <h6 className="mb-1 text-start">{board.name}</h6>
                    <div className="small text-body-secondary text-start mb-2">,    </div>

                    <div className="tracker-board-kpis">
                      <span><i className="bi bi-list-check me-1" />{boardStats.total}</span>
                      <span><i className="bi bi-hourglass-split me-1" />{boardStats.progress}</span>
                      <span><i className="bi bi-check2-circle me-1" />{boardStats.done}</span>
                    </div>
                  </article>
                </div>
              );
            })}

            {boards.length === 0 && (
              <div className="col-12">
                <div className="card shadow-sm">
                  <div className="card-body text-body-secondary text-center py-4">
                      
                  </div>
                </div>
              </div>
            )}
          </div>

          {boardPagination.totalPages > 1 && (
            <div className="d-flex justify-content-center mt-4">
              <div className="btn-group" role="group" aria-label="Boards pagination">
                <button
                  type="button"
                  className="btn btn-outline-primary"
                  disabled={boardPagination.page <= 1}
                  onClick={() => setBoardPage((prev) => Math.max(1, prev - 1))}
                >
                  
                </button>
                <button type="button" className="btn btn-primary" disabled>
                  {boardPagination.page} / {boardPagination.totalPages}
                </button>
                <button
                  type="button"
                  className="btn btn-outline-primary"
                  disabled={boardPagination.page >= boardPagination.totalPages}
                  onClick={() => setBoardPage((prev) => Math.min(boardPagination.totalPages, prev + 1))}
                >
                  
                </button>
              </div>
            </div>
          )}
        </div>

        {isBoardModalOpen && (
          <div className="modal-overlay" onClick={closeBoardModal}>
            <div className="task-modal card shadow-lg" onClick={(event) => event.stopPropagation()}>
              <div className="card-body">
                <div className="d-flex justify-content-between align-items-center mb-3">
                  <h5 className="mb-0">{boardModalMode === 'edit' ? ' ' : ' '}</h5>
                  <button type="button" className="btn btn-sm btn-outline-secondary" onClick={closeBoardModal}>
                    <i className="bi bi-x-lg" />
                  </button>
                </div>

                <form className="row g-3" onSubmit={submitBoard}>
                  <div className="col-12">
                    <label className="form-label"> </label>
                    <input
                      className="form-control"
                      value={boardDraft.name}
                      onChange={(event) => setBoardDraft((prev) => ({ ...prev, name: event.target.value }))}
                      placeholder=": , , "
                    />
                  </div>

                  <div className="col-12">
                    <label className="form-label"></label>
                    <div className="tracker-icon-grid">
                      {BOARD_ICON_OPTIONS.map((icon) => (
                        <button
                          key={icon}
                          type="button"
                          className={`tracker-icon-option ${boardDraft.icon === icon ? 'active' : ''}`}
                          onClick={() => setBoardDraft((prev) => ({ ...prev, icon }))}
                        >
                          <i className={`bi ${icon}`} />
                        </button>
                      ))}
                    </div>
                  </div>

                  <div className="col-12">
                    <label className="form-label"> </label>
                    <div className="tracker-color-grid">
                      {BOARD_COLOR_OPTIONS.map((color) => (
                        <button
                          key={color.id}
                          type="button"
                          className={`tracker-color-option ${boardDraft.colorId === color.id ? 'active' : ''}`}
                          style={{ background: color.soft, borderColor: color.accent }}
                          title={color.label}
                          onClick={() => setBoardDraft((prev) => ({ ...prev, colorId: color.id }))}
                        >
                          <span style={{ background: color.accent }} />
                        </button>
                      ))}
                    </div>
                  </div>

                  <div className="col-12 d-flex justify-content-end gap-2">
                    <button type="button" className="btn btn-outline-secondary" onClick={closeBoardModal}>
                      
                    </button>
                    <button type="submit" className="btn btn-primary">
                      {boardModalMode === 'edit' ? '' : ''}
                    </button>
                  </div>
                </form>
              </div>
            </div>
          </div>
        )}
      </>
    );
  }

  return (
    <>
      {!selectedBoard ? (
        <div className="card shadow-sm">
          <div className="card-body text-center py-5">
            <div className="text-body-secondary mb-3">  </div>
            <button type="button" className="btn btn-primary" onClick={() => setViewMode('boards')}>
              <i className="bi bi-plus-circle me-2" />
               
            </button>
          </div>
        </div>
      ) : (
      <div className="d-flex flex-column gap-4" style={{ '--board-accent': selectedBoardColor.accent, '--board-soft': selectedBoardColor.soft }}>
        <div className="card shadow-sm tracker-toolbar-card tracker-board-toolbar">
          <div className="card-body d-flex flex-wrap align-items-center justify-content-between gap-3">
            <div>
              <div className="d-flex align-items-center gap-2 mb-1">
                {selectedBoard && (
                  <span className="tracker-selected-board-icon">
                    <i className={`bi ${selectedBoard.icon}`} />
                  </span>
                )}
                <h5 className="mb-0">{selectedBoard?.name || ''}</h5>
              </div>
              <div className="small text-body-secondary">
                 : {stats.total}, : {stats.done}
              </div>
            </div>
            <div className="d-flex gap-2 flex-wrap">
              <button type="button" className="btn btn-outline-primary" onClick={() => setViewMode('boards')}>
                <i className="bi bi-grid me-2" />
                 
              </button>
              {selectedBoard && (
                <button type="button" className="btn btn-outline-secondary" onClick={() => openEditBoardModal(selectedBoard)}>
                  <i className="bi bi-pencil me-2" />
                  
                </button>
              )}
              {selectedBoard && (
                <button type="button" className="btn btn-outline-danger" onClick={() => handleDeleteBoard(selectedBoard)}>
                  <i className="bi bi-trash me-2" />
                  
                </button>
              )}
              <button type="button" className="btn btn-primary" onClick={openCreateTaskModal}>
                <i className="bi bi-plus-circle me-2" />
                 
              </button>
            </div>
          </div>
        </div>

        <div className="kanban-grid">
          {Object.keys(statusMeta).map((status) => {
            const columnTasks = tasks.filter((task) => task.status === status);
            return (
              <section
                className={`kanban-column ${dragOverStatus === status ? 'drag-over' : ''}`}
                key={status}
                onDragOver={(event) => {
                  event.preventDefault();
                  setDragOverStatus(status);
                }}
                onDragLeave={() => setDragOverStatus(null)}
                onDrop={(event) => {
                  event.preventDefault();
                  handleDrop(status);
                }}
              >
                <div className="kanban-header">
                  <div>
                    <h6 className="mb-0">{statusMeta[status].label}</h6>
                    <small className="text-body-secondary">{columnTasks.length} </small>
                  </div>
                </div>
                <div className="kanban-cards">
                  {columnTasks.length === 0 ? (
                    <div className="empty-column">  </div>
                  ) : (
                    columnTasks.map((task) => (
                      <article
                        className="task-card draggable-task"
                        key={task.id}
                        draggable
                        onDragStart={() => setDragTaskId(task.id)}
                        onDragEnd={() => {
                          setDragTaskId(null);
                          setDragOverStatus(null);
                        }}
                      >
                        <div className="d-flex justify-content-between gap-2">
                          <h6 className="mb-1">{task.title}</h6>
                          <button
                            type="button"
                            className="btn btn-sm btn-outline-danger"
                            onClick={() => onTaskDelete(task.id)}
                            title=""
                          >
                            <i className="bi bi-trash" />
                          </button>
                        </div>
                        {task.description && <p className="task-desc">{task.description}</p>}
                        <div className="d-flex gap-2 flex-wrap mb-2">
                          <span className={`badge ${priorityMeta[task.priority].className}`}>
                            {priorityMeta[task.priority].label}
                          </span>
                          <span className="badge text-bg-light">+{task.points} </span>
                          <span className="badge text-bg-secondary"> {task.dueDate}</span>
                        </div>
                        <div className="d-flex justify-content-between align-items-center gap-2">
                          <select
                            className="form-select form-select-sm"
                            value={task.status}
                            onChange={(event) => onTaskStatusChange(task.id, event.target.value)}
                          >
                            <option value="todo"> </option>
                            <option value="progress"> </option>
                            <option value="done"></option>
                          </select>
                          {task.rewardClaimed && (
                            <span className="small text-success fw-semibold">
                              <i className="bi bi-check2-circle me-1" />
                               
                            </span>
                          )}
                        </div>
                      </article>
                    ))
                  )}
                </div>
              </section>
            );
          })}
        </div>
      </div>
      )}

      {isCreateTaskModalOpen && (
        <div className="modal-overlay" onClick={closeCreateTaskModal}>
          <div className="task-modal card shadow-lg" onClick={(event) => event.stopPropagation()}>
            <div className="card-body">
              <div className="d-flex justify-content-between align-items-center mb-3">
                <h5 className="mb-0"> </h5>
                <button type="button" className="btn btn-sm btn-outline-secondary" onClick={closeCreateTaskModal}>
                  <i className="bi bi-x-lg" />
                </button>
              </div>

              <form className="row g-3" onSubmit={submitCreateTask}>
                <div className="col-12">
                  <label className="form-label"></label>
                  <input
                    className="form-control"
                    value={taskForm.title}
                    onChange={(event) => onTaskFormChange('title', event.target.value)}
                    placeholder=":  "
                  />
                </div>
                <div className="col-md-6">
                  <label className="form-label"></label>
                  <select
                    className="form-select"
                    value={taskForm.priority}
                    onChange={(event) => onTaskFormChange('priority', event.target.value)}
                  >
                    <option value="low"></option>
                    <option value="medium"></option>
                    <option value="high"></option>
                  </select>
                </div>
                <div className="col-md-6">
                  <label className="form-label"></label>
                  <input
                    type="date"
                    className="form-control"
                    value={taskForm.dueDate}
                    onChange={(event) => onTaskFormChange('dueDate', event.target.value)}
                  />
                </div>
                <div className="col-12">
                  <label className="form-label"></label>
                  <textarea
                    className="form-control"
                    rows={3}
                    value={taskForm.description}
                    onChange={(event) => onTaskFormChange('description', event.target.value)}
                    placeholder="  "
                  />
                </div>
                <div className="col-12 d-flex justify-content-end gap-2">
                  <button type="button" className="btn btn-outline-secondary" onClick={closeCreateTaskModal}>
                    
                  </button>
                  <button type="submit" className="btn btn-primary">
                    
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>
      )}

      {isBoardModalOpen && (
        <div className="modal-overlay" onClick={closeBoardModal}>
          <div className="task-modal card shadow-lg" onClick={(event) => event.stopPropagation()}>
            <div className="card-body">
              <div className="d-flex justify-content-between align-items-center mb-3">
                <h5 className="mb-0">{boardModalMode === 'edit' ? ' ' : ' '}</h5>
                <button type="button" className="btn btn-sm btn-outline-secondary" onClick={closeBoardModal}>
                  <i className="bi bi-x-lg" />
                </button>
              </div>

              <form className="row g-3" onSubmit={submitBoard}>
                <div className="col-12">
                  <label className="form-label"> </label>
                  <input
                    className="form-control"
                    value={boardDraft.name}
                    onChange={(event) => setBoardDraft((prev) => ({ ...prev, name: event.target.value }))}
                  />
                </div>
                <div className="col-12">
                  <label className="form-label"></label>
                  <div className="tracker-icon-grid">
                    {BOARD_ICON_OPTIONS.map((icon) => (
                      <button
                        key={icon}
                        type="button"
                        className={`tracker-icon-option ${boardDraft.icon === icon ? 'active' : ''}`}
                        onClick={() => setBoardDraft((prev) => ({ ...prev, icon }))}
                      >
                        <i className={`bi ${icon}`} />
                      </button>
                    ))}
                  </div>
                </div>
                <div className="col-12">
                  <label className="form-label"> </label>
                  <div className="tracker-color-grid">
                    {BOARD_COLOR_OPTIONS.map((color) => (
                      <button
                        key={color.id}
                        type="button"
                        className={`tracker-color-option ${boardDraft.colorId === color.id ? 'active' : ''}`}
                        style={{ background: color.soft, borderColor: color.accent }}
                        onClick={() => setBoardDraft((prev) => ({ ...prev, colorId: color.id }))}
                      >
                        <span style={{ background: color.accent }} />
                      </button>
                    ))}
                  </div>
                </div>
                <div className="col-12 d-flex justify-content-end gap-2">
                  <button type="button" className="btn btn-outline-secondary" onClick={closeBoardModal}></button>
                  <button type="submit" className="btn btn-primary">{boardModalMode === 'edit' ? '' : ''}</button>
                </div>
              </form>
            </div>
          </div>
        </div>
      )}
    </>
  );
}
